body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.ant-upload-list-item-done {
  cursor: pointer;
}

.leaflet-pane {
  z-index: 0 !important;
}

.leaflet-control {
  z-index: 1 !important;
}

.leaflet-marker-photo {
  border: 2px solid #fff;
  box-shadow: 3px 3px 10px #888;
}

.leaflet-marker-photo div {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.leaflet-marker-photo b {
  position: absolute;
  top: -7px;
  right: -11px;
  color: #555;
  background-color: #fff;
  border-radius: 8px;
  height: 12px;
  min-width: 12px;
  line-height: 12px;
  text-align: center;
  box-shadow: 0 3px 14px rgba(0, 0, 0, 0.4);
}

.leaflet-popup-photo .leaflet-popup-content-wrapper {
  padding: 6px;
  border-radius: 4px;
}
.leaflet-popup-photo .leaflet-popup-content {
  margin: 0;
}
.leaflet-popup-photo .leaflet-popup-tip {
  background: white;
}
.leaflet-popup-photo img,
.leaflet-popup-photo video {
  display: block;
  width: 100%;
}

.leaflet-popup-photo .leaflet-popup-close-button {
  padding: 7px 7px 0 0 !important;
}

